@font-face {
  font-family: 'Proxima Nova';
  src: url(./fonts/proxima-nova-font.otf) format('opentype');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

:root {
  --light-color: #F8F9FA;
  --medium-color: #36393f;
  --dark-color: #0D1117;
  --red: #e06666;
  --green: #93c47d;
  --yellow: #dec354;
}

body {
  font-family: 'Proxima Nova';
  background-color: var(--medium-color);
  color: var(--light-color);
}

.spinner-border {
  width: 20rem;
  height: 20rem;
  color: var(--light-color);
}

.btn-group-justified > label {
  width: 100%;
}

.btn-group-sm>.btn, .btn-sm {
  padding: 0.5rem 0.5rem 0.25rem;
}

.btn-check:checked+.btn-outline-not-finished,
.btn-check:hover+.btn-outline-not-finished {
  color: var(--light-color);
  background-color: var(--red);
  border-color: var(--red);
}

.btn-outline-not-finished {
  color: var(--red);
  border-color: var(--red);
}

.btn-check:checked+.btn-outline-first-to-finish,
.btn-check:hover+.btn-outline-first-to-finish {
  color: var(--light-color);
  background-color: var(--yellow);
  border-color: var(--yellow);
}

.btn-outline-first-to-finish {
  color: var(--yellow);
  border-color: var(--yellow);
}

.btn-check:checked+.btn-outline-finished,
.btn-check:hover+.btn-outline-finished {
  color: var(--medium-color);
  background-color: var(--green);
  border-color: var(--green);
}

.btn-outline-finished {
  color: var(--green);
  border-color: var(--green);
}

.form-switch .form-check-input {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e");
  background-color: var(--medium-color);
  border-color: var(--light-color);
}
